import React, { Component } from "react";
import "./Banner.scss";

class Banner extends Component {
  constructor(props) {
    super(props);
    this.state = {
      bannerHeight: window.innerHeight - (64 * 2)
    }
    this.handleButtonClick = this.handleButtonClick.bind(this);
    this.handleKeyPress = this.handleKeyPress.bind(this);
  }
  handleButtonClick() {
    const targetElem = document.querySelector('.md-grid');
    console.log(targetElem.offsetTop);
    window.scrollTo(0, targetElem.offsetTop - 64);
  }
  handleKeyPress() {
    this.handleButtonClick();
  }
  render() {
    const { fileEdges } = this.props;
    const imageUrl = fileEdges[58].node.childImageSharp.sizes.src;
    console.log(fileEdges[58].node);
    return (
      <div
        className="banner" 
        style={{
          backgroundImage: `url(${imageUrl})`,
          height: this.state.bannerHeight,
        }}
      >
        <div className="content">
          <hgroup>
            <h1>EqualD ONEDAY CLASS × CAFE LUXEMBROUG</h1>
            <p>7th - 31st, JAN. 2019</p>
          </hgroup>

        </div>
        <button className="button" onClick={this.handleButtonClick} onKeyPress={this.handleKeyPress}>
          Class List ▼
        </button>
      </div>
    )
  }
}

export default Banner;



